.music {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
} 
.music .imageAlbum img {
    width: 200px;
    height: 200px;
    border-radius: 20px;
    padding: 10px;
    object-fit: fit-content;
}
.music .imageAlbum {
    display: flex;
    flex-wrap:wrap;
    justify-content: center;
    align-items: center;
    margin-bottom: 200px;
} 
@media screen and (max-width:400px) {
    .music .imageAlbum {
        margin-top: 50px;
    }
}
@media screen and (max-width:300px) {
    .music .imageAlbum {
        margin-top: 100px;
    }
}
@media screen and (max-width:600px) {
    .music .imageAlbum img {
    width: 170px;
    height: 170px;
} 
}  
.modelAlbum, .modelAlbum1, .modelAlbum2 {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: white;
    z-index: 2000;
    visibility: hidden;
    opacity: 0;
    transform: scale(0); 
    transition: opacity 0.4s ease, visibility 0.5s ease, transform 0.4s ease-in-out;
    overflow: hidden;
}
.modelAlbum.open, .modelAlbum1.open, .modelAlbum2.open {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
}
.player{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: black;
    padding: 20px;
    border-radius: 10px; 
    width: 375px;
}
@media screen and (max-width:375px) {
    .player {
        width: 300px;
    }
    .player audio {
        width: 280px;
    }
}
@media screen and (max-width:300px) {
    .player {
        width: 250px;
    }
    .player audio,.player img{
        width: 230px;
    }
}
@media screen and (max-width:250px) {
    .player {
        width: 200px;
    }
    .player audio,.player img{
        width: 200px;
    }
    .player h3 {
        font-size: 16px;
    }
}
.player h3 {
    color: white;
    margin: 5px;
    text-align: center;
}
.player audio{
    margin: 10px 0 30px;
}
.player img {
    border-radius: 5px;
    object-fit: cover;
}
.skip {
    display: flex;
    width: 100px;
    justify-content: space-evenly;
}
.imageAlbum {
    display: flex;    
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.imageAlbum img {
    border-radius: 20px;
    background-size: cover;
    background-position: center center;
    padding: 10px;
    width: 250px;
    height: 250px;
}