.c-player {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: black;
    margin: 0px auto;
    padding: 10px 50px; 
    border-radius: 16px;
    width: 300px;
    height: 500px;
}
.c-player > h4 {
    color: #FFF;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 500;
    text-align: center;
    margin-bottom: 0;
}
.c-player > p {
    color: #AAA;
    font-size: 14px;
    text-align: center;
    font-weight: 600;
}

.c-player > p span {
    font-weight: 400;
}

.c-player--details .details-img {
    position: relative;
    width: 250px;
    margin: 0 auto;
}

.c-player--details .details-img .imageSource {
    display: block;
    margin: 15px auto 0;
    width: 100%;
    max-width: 200px;
    border-radius: 20px;
}

.c-player--details .details-img:after {
    content: '';
    display: block;
    position: absolute;
    top: -3px;
    left: -1px;
    right: -1px;
    bottom: -3px;
    border-radius: 5px;
    border: 3px dashed white;
}

.c-player--details .details-title {
    color: #EEE;
    font-size: 24px;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.8), -2px -2px 4px rgba(255,255,255,0.4);
    text-align: center;
    margin: 5px 0 3px;
}

.c-player--details .details-artist {
    color: #AAA;
    font-size: 20px;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.8), -2px -2px 4px rgba(255,255,255,0.4);
    text-align: center;
    margin: 3px 0 6px;
}

.c-player--controls {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
}

.c-player--controls .skip-btn {
    display: inline-block;
    padding: 0 10px;
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
    color: #888;
    font-size: 18px;
}
@media screen and (max-width:400px) {
    .c-player {
        width: 300px;
    }
    .c-player audio {
        width: 250px;
    }
}
@media screen and (max-width:300px) {
    .c-player {
        width: 250px;
    }
    .c-player audio {
        width: 200px;
    }
}
.c-player audio {
    height: 50px;
}
