.contact {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.iconContact {
    margin: 10px 0 30px;
}
.form {
    margin-bottom: 150px;
}
.contentForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    background-color: black;
    border-radius: 10px;
}
.contentForm input,textarea{
    background-color: transparent;
    color: white;
    outline: none;
    border: 1px solid white; 
    width: 300px;
    padding: 10px 20px;
    margin-bottom: 30px;
    border-radius: 5px;
}
.contentForm input{ 
    height: 30px; 
}
.contentForm input[type="submit"]{
    background-color: #398AB9;
    text-transform: uppercase;
}
textarea{
    height: 150px;
}
.contentForm input:focus{
    background-color: #398ab977;
    border-color: #398AB9; 
}
.msg:focus{
    background-color: #398ab977;
    border-color: #398AB9;
}
@media screen and (max-width:350px) {
    .contentForm input {
        width: 250px;
    }
    textarea{
        width: 250px !important;
    }
    .msgSend  {
        width: 200px;
    }
}
@media screen and (max-width:283px) {
    .contentForm input {
        width: 200px;
    }
    textarea{
        width: 200px !important;
    }
     .msgSend  {
        width: 150px;
    }
}
.msgSend {
    color: #65C18C;
    text-transform: capitalize;
    text-align: center;
    width: 300px;
}