.footer {
    position: relative; 
    width: 100%;
    min-height: 100px;
    background-color:black;
    display: flex;
    justify-content: center;
    align-items: center;
}
@media screen and (max-width:992px) {
    .footer {
        z-index: 10;
    }
}
.footer .wave {
    position: absolute;
    top: -100px;
    left: 0;
    background: url(../assets/wave.png);
    width: 100%;
    height: 100px;
    background-size: 1000px 100px;
}
.footer .wave#wave1 {
    z-index: 1000;
    opacity: 1;
    bottom: 0;
    animation: animateWave 4s linear infinite;
}
.footer .wave#wave2 {
    z-index: 999;
    opacity: 0.5;
    bottom: 10px;
    animation: animateWave_2 4s linear infinite;
}
.footer .wave#wave3 {
    z-index: 1000;
    opacity: 0.2;
    bottom: 15px;
    animation: animateWave 3s linear infinite;
}
.footer .wave#wave4 {
    z-index: 999;
    opacity: 0.7;
    bottom: 20px;
    animation: animateWave_2 3s linear infinite;
}
@keyframes animateWave {
    0% {
        background-position-x: 1000px;
    }
    100% {
        background-position-x: 0;
    }
}
@keyframes animateWave_2 {
    0% {
        background-position-x: 0;
    }
    100% {
        background-position-x: 1000px;
    }
}
.logo {
    width: 150px;
    height: 150px;
    background: url(../assets/profil.jpeg);
    background-size: contain;
    background-position: center center;
    border-radius: 50%;
}
.infos{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.social-media {
    padding: 30px 0 0;
}
.social-media .fa {
    margin-right: 15px;
    font-size: 40px;
    color: white;
}
.social-media .fa:hover {
    transform: translateY(-10px);
    transition: 0.3s;
    cursor: pointer;
    color: var(--main-color) !important;
}
@media screen and (max-width:329px) {
    .copyright p {
        text-align: center;
    }
}