.scroll {
    background-color: var(--main-color);
    width: 40px;
    height: 40px; 
    border-radius: 100%;
    position: fixed;
    right: 0;
    top: 500px; 
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; 
    cursor: pointer;
}
.scroll .top {
    animation: animateTop 1s alternate ease infinite;
    transform: 0.3s;
}
@keyframes animateTop {
    from {
        transform: translateY(2px);
    }
    to {
        transform: translateY(-1px);
    }
}