.landing img {
    position: fixed;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
    animation: animateLanding 7s ease-in-out infinite;
    
}
.landing::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 110vh; 
    background-color: rgba(0, 0, 0, 0.555);
    z-index: 1;
}
@keyframes animateLanding {
    0% {
        transform: scale(1); 
    }
    100% {
        transform: scale(2);  
    }
}
.landing {
    height: 100vh;
    width: 100%;
    object-fit: contain;
}
.landing .content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 30vh;
    text-align: center;
    margin: auto;
    width: 100%;
    z-index: 100;
}
.landing .content h1 {
    font-size: 4rem;
    text-transform: capitalize;
}
.landing .content p {
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
    margin-bottom: 1.6rem;
}
.btn {
    border: 1px solid white;
    padding: 4px 25px;
    font-weight: 600;
    border-radius: 30px;
    font-size: 1.4rem;
    cursor: pointer;
}
.btn:hover {
    box-shadow: 0 0 5px var(--main-color), 0 0 15px var(--main-color), 0 0 30px var(--main-color);
    background-color: var(--main-color);
}
@media  screen and (max-width:379px) {
    .landing .content h1 {
    font-size: 2rem !important;
}
}
@media  screen and (max-width:616px) {
    .landing .content h1 {
    font-size: 2.5rem;
}}
@media  screen and (max-width:304px) {
    .landing .content h1 {
    font-size: 1.5rem !important;
}}
.landing .content p {
    font-size: 1rem;
    font-weight: 200;
}
.follow p{
    padding-top: 30px;
    margin: 0 !important;
    color:#398AB9;
    font-weight: 900;
    letter-spacing: 2px;
}
.follow .down{
    background-color: var(--main-color);
    padding: 4px;
    border-radius: 50%;
    animation: animateDown 1s ease-in infinite;
    transform: 0.3s;
    cursor: pointer;
}
@keyframes animateDown {
    0% {
        transform: translateY(-7px);
    }
    100% {
        transform: translateY(5px);
        transform: scale(1.2);
    }
}
