.gallery {
    background-color: white;
    padding-bottom: 200px;
}
.gallery .button{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
}
.gallery .button button {
    background-color: black;
    padding: 5px;
    width: 100px;
    color: white;
    border: none;
    margin: 0 5px;
    border-radius: 5px;
    cursor: pointer;
}
.buttonChange {
    background-color: var(--main-color) !important;
}
.gallery img {
    width: 300px;
    height: 300px;
    padding: 5px;
    border-radius: 10px;
    cursor: pointer;
}
.gallery video {
    width: 300px;
    height: 300px;
    padding: 5px;
}
.gallery .content {
   display: flex;
   justify-content:center;
   align-items: center;
   flex-wrap: wrap;
}
.gallery .content p{
    color: black;
}
@media screen and (max-width:300px) {
    .gallery img {
        width: 200px;
        height: 200px;
    }
    .gallery video {
    width: 200px;
    height: 200px;
}
}
/*Model*/
.model {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: black;
    z-index: 2000;
    visibility: hidden;
    opacity: 0;
    transform: scale(0); 
    transition: opacity 0.4s ease, visibility 0.5s ease, transform 0.4s ease-in-out;
    overflow: hidden;
}
.model.open {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
}
.model img {
    width: 350px;
    height: 350px;
}
.model p {
    color: white;
    text-align: center;
    line-height: 1.7;
    padding: 4px;
}
.model p::first-letter {
    text-transform: capitalize;
    font-size: 1.2rem;
}
@media screen and (max-width:900px) {
    .model img {
    width: 300px;
    height: 300px;
}
}
@media screen and (max-width:600px) {
    .model img {
    width: 250px;
    height: 250px;
}
}
 @media screen and (max-width:256px) {
    .model img {
    width: 170px;
    height: 170px;
} 
}
.model p {
    margin: 0;
}
.close {
    cursor: pointer;
}
