.header {
    position: sticky;
    top:0;
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    z-index: 1000; 
    cursor: pointer;
     background-color: black;
}
.nav-menu {
    display: flex;
}
.nav-menu li {
    padding: 0 1.3rem;
}
.nav-menu li:hover {
    transform: scale(1.1);
    background-color: var(--main-color);
    border-radius: 5px;
    transition: 0.3s;
}
.nav-menu a {
    font-size: 1.2rem;
    font-weight: 500;
}
.nav-bar {
    display: none;
}
@media screen and (max-width:992px) {
    .nav-bar {
        display: flex;
        align-items: center;
    }
    .nav-menu {
        flex-direction: column;
        width: 100%;
        height: 100vh;
        position: fixed;
        justify-content: center;
        align-items: center;
        top: 10px;
        left: -100%;
        text-align: center;
        z-index: -1;
        transition: 0.3s;
        background-color: black;
    }
    .nav-menu.active{
        left: 0;
    }
    .nav-menu li {
        padding: 0.8rem 1.4rem;   
        border-radius: 10px;
        width: 100%;
    }
    .nav-menu a {
        font-size: 2rem;
    }
    .nav-menu li:hover {
        background-color: #3586ff;
    }
}
.lang .nav-lang li {
    color: black;
}
.lang .nav-lang li:not(:last-of-type) {
    border-bottom: 1px solid black;
}
.lang .nav-lang li:hover{
    transform: scale(1.1);
    color: var(--main-color);
    transition: 0.3s;
}
.lang .nav-lang {
    display: none;
}
.lang ul{
    position: absolute;
    top: 65px;
    right: 17px;
    background-color: white;
    padding: 10px 15px;
    border-radius: 5px 0 5px 5px;
}
.lang ul::after {
    content: '';
    position: absolute;
    right: 0;
    top: -10px;
    width: 5px;
    height: 5px;
    border-style: solid ;
    border-width: 15px;
    border-color:transparent  white transparent transparent;
}
@media screen and (max-width:992px) {
    .lang{
        display: flex;
        flex: 1;
        justify-content: right;
        align-items: center;
    }
    .lang ul {
        right:50px;
    }
    .language-icon {
        padding-right: 10px;
    }
}
@media screen and (max-width:329px) {
     .lang{
         justify-content: center;
     }
     .lang ul{
         width: 100%;
         left: 0;
         border-radius: 5px;
     }
     .lang ul::after {
         display: none;
     }
     .lang .nav-lang li:hover{
         transform: translateX(10px);
     }
}
.lang .nav-lang.activelang {
    display: block;
}
.language-icon {
    display: flex;
}