.about {
    background-color: white;
}
.headingIcon {
    display: flex;
    justify-content: center;
    height: 100px;
    align-items: center;
    padding-bottom: 40px;
}
.contentAbout {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80%;
    padding: 10px 20px 130px;
    margin: auto;
}
.contentAbout .imageAbout::after {
    content: '';
    position: absolute;
    top: -20px;
    right: -20px;
    width: 50%;
    height: calc(100% + 10px);
    border-width: 10px;
    border-color: black;
    border-style: solid;
    z-index: -1;
}
.contentAbout .imageAbout {
    position: relative;
    z-index: 1;
}
@media screen and (max-width:900px) {
    .contentAbout .imageAbout img {
        width: 200px;
        height: 300px;
    }
    .contentAbout {
        align-items: flex-start;
        width: 100%;
    }
}
.contentAbout .content {
    padding-left: 70px;
}
.contentAbout .content p {
    color: black;
    text-align: justify;
    line-height: 1.7;
    padding-bottom: 30px;
}
@media screen and (max-width:500px) {
    .contentAbout .content p {
        text-align: center;
    }
}
@media screen and (max-width:600px) {
    .contentAbout {
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;
    }
    .contentAbout .content {
        padding: 40px 0 0;
}
}
@media screen and (max-width:242px) {
    .contentAbout .imageAbout::after {
        right: 50%;
        transform: translateX(50%);
        overflow: hidden;
    }
}
.links a {
    color: black;
    font-size: 24px;
    text-transform: uppercase;
    font-weight: bold;
    text-decoration: underline;
    cursor: pointer;
}
.links a:last-of-type {
       padding-left:  20px;
}
.links a:hover {
    color: var(--main-color);
}
@media screen and (max-width:620px) {
    .links a {
        font-size: 20px;
        padding: 0 !important;
    }
    .links {
        display: flex;
        flex-direction: column;
    }
}
