.fa {
    color: var(--main-color);
}
.headlandPortfolio {
    height: 30vh; 
    background-color: white;
    display: flex;
    justify-content: center;
    width: 100%;
}
@media screen and (max-width:326px) {
    .headlandPortfolio {
        height: 35vh;
    }
}
.head{
    position: absolute;
    top: 100px;
    text-align: center;   
}
.head::after{
    content: '';
    position: absolute;
    bottom: -20px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: var(--main-color);  
}
.head::before{
    content: '';
    position: absolute;
    bottom: -30px;
    left: 50%;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    transform: translateX(-50%);
    background-color: var(--main-color);  
}
.head p{
    color: black;
    width: 400px;
    font-weight: 500;
    text-transform: capitalize;
}
@media screen and (max-width:400px) {
    .head p{
    width: 300px;
}
}
@media screen and (max-width:300px) {
    .head p{
    width: 200px;
}
}